/* base styles */

body { background: #fff; padding: 0; margin: 0; }

.main-header { background: #fff none; text-align: center; }



.logo { display: block;
	span { height:0;padding-bottom: 30.58%; width: 100%; display: block; text-indent: -9999px; background: transparent url('images/srny.png') left top no-repeat; background-size: cover; }
}

@media (min-width:480px){
	.main-header { background-image: url('images/banner.jpg'); background-size: cover; }
	.logo { display: inline-block; max-width: 100%; width: 484px; }
}
 
